import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from './../../../../environment';
@Injectable({
  providedIn: 'root'
})
export class BecaService {
  urlCreateBeca=  environment.URL_LOCAL+'/app/componentes/becas/create_becas.php';
  urlListTBeca = environment.URL_LOCAL+'/app/componentes/tipobecas/list_tipobecas.php';
  constructor(private http: HttpClient) { }

  
  registrarBecas(registroBeca:Array<any>){
    return this.http.post(`${this.urlCreateBeca}`,{data: registroBeca}).
    pipe(map((res)=>{
      return  registroBeca
    }));
  }
  buscarListadoTBecas(){
    return this.http.get(`${this.urlListTBeca}`);
  }
}
